import React, { FC } from 'react';
import { ColumnsProps, DefaultSortProps, FilterType, getExtraData, OrderType, SearchDataGrid, SearchViewProps, WindowComponent } from 'summer';
import { CLASS_NAME, ConfigIrpj } from '../../../../models/dtos/mlf/configIrpj';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'descricao',
    order: OrderType.ASC,
  },
];

const ConfigIrpjSearchView: FC<SearchViewProps<ConfigIrpj>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    {
      dataField: 'codigoReceita',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código da Receita',
    },
    {
      dataField: 'periodoApuracao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Período de Apuração',
    },
    {
      dataField: 'diaVencimento',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Dia de Vencimento',
    },
    {
      dataField: 'aliquota',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Alíquota',
    },
    {
      dataField: 'recolhimentoMinimo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Recolhimento Mínimo',
    },
  ];
  return (
    <WindowComponent title="Configurações de IRPJ">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'ConfigIrpjEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default ConfigIrpjSearchView;
